import React,{Component}from 'react';
import './home.css';
import Mytab from './Mytab';
import {
    NavLink,Route, Redirect, Router,

} from 'react-router-dom'
import { SearchBar,Carousel } from 'antd-mobile'
// import { render } from '@testing-library/react';
// import { Component } from 'react';
// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>
class Home extends React.Component{
    state = {
        data: ['1', '2', '3'],
        imgHeight: 176,
      }
     
    render(){
        var goodsList = [
            {
                id:1,
                img:'i1/741494722/O1CN01omV8ta1kkikurV9Qq_!!0-item_pic.',
                title:'创意厨房调味盒',
                money:38,
                sale:'70人已购买'
            },
            {
                id:2,
                img:'i3/TB1r_3UJFXXXXXWXFXXXXXXXXXX_!!0-item_pic.',
                title:'韩国进口水杯马克杯',
                money:45,
                sale:'20人已购买'
            },
            {
                id:3,
                img:'i1/3866830176/O1CN01ji3Ggb1DAeKxvcS1B_!!3866830176-0-picasso.',
                title:'中式碟碗套装',
                money:189,
                sale:'90人已购买'
            },
            {
                id:4,
                img:'i1/390568363/O1CN01Xmavq82BeIvBBs7g9_!!0-item_pic.',
                title:'金色相框植物标本',
                money:19,
                sale:'150人已购买'
            },
            {
                id:5,
                img:'i1/473301157/TB2Vjvzd7fb_uJkSmFPXXcrCFXa_!!473301157.',
                title:'粉红豹易拉罐创意水杯',
                money:35,
                sale:'200人已购买'
            },
            {
                id:6,
                img:'i1/2730507098/O1CN016am0U222IvqConYkd_!!2730507098.',
                title:'孔雀壁挂钟',
                money:89,
                sale:'120人已购买'
            },
            {
                id:7,
                img:'i1/2519249932/O1CN012Y2TqY2NEuI3KANkT_!!2519249932.',
                title:'梦幻公主窗帘',
                money:289,
                sale:'62人已购买'
            },
            {
                id:8,
                img:'i3/2122048702/O1CN01tAdyzF2E9Z73m7MPa_!!0-item_pic.',
                title:'玻璃陶瓷插花花瓶',
                money:130,
                sale:'42人已购买'
            },
            {
                id:9,
                img:'i2/671025616/O1CN01ANGCde1rMArSdd3cv_!!671025616.',
                title:'田园床上用品',
                money:259,
                sale:'23人已购买'
            },
            {
                id:10,
                img:'i3/2535632730/TB2n67XGY5YBuNjSspoXXbeNFXa_!!2535632730.',
                title:'复古蓝刻花方碗',
                money:25,
                sale:'50人已购买'
            },
        ]
    return (
        <div className='home'>
            <header className='theTop'>
                <NavLink to='/searchbar'>
                <div className='searchbar'>
                    <a className='search'>
                        <span>
                            寻找宝贝店铺
                        </span>
                        
                    </a>
                </div>
                </NavLink>
            </header>
            <div className='Carousel'>
                <Carousel className="space-carousel"
                    autoplay
                    infinite
                >
                    {this.state.data.map((val, index) => (
                        <a
                            key={val}
                            style={{
                                display: 'block',
                                position: 'relative',
                                height: this.state.imgHeight
                            }}
                        >
                            <img
                                src={require(`./image/${val}.jpg`)}
                                alt=""
                                style={{ width: '100%',height:'120px'}}
                                onLoad={() => {
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        </a>
                ))}
                </Carousel>
            </div>
            <div className='goods'>
                
                    {
                        goodsList.map(goods =>{
                            return(
                            <div
                                style={{
                                        height: '260px',
                                        position: 'relative',
                                        width: '170px',
                                        boxSizing:'border-box',
                                        display: '-webkit-flex',
                                        display: 'flex',
                                        WebkitBoxOrient: 'vertical',
                                        WebkitBoxDirection: 'normal',
                                        WebkitFlexDirection: 'column',
                                        flexDirection: 'column',
                                        alignContent: 'flex-start',
                                        flexShrink:'0',
                                        backgroundColor: '#fff',
                                        marginTop: '9px',
                                        borderRadius: '6px',
                                        overflow: 'hidden',
                                        marginLeft: '4.5px',
                                        marginRight: '4.5px',
                                }}
                                // selected={ pathname === '/goods' }
                                onClick={()=>this.routerTo(goods)}
                                >
                                <div className='image'>
                                    <img className='pic'
                                    src={`//gw.alicdn.com/bao/uploaded/${goods.img}jpg_500x500q90.jpg_.webp`}/>
                                </div>
                                <div className='title'>
                                    <div className='txt'>
                                        <span>{goods.title}</span>
                                    </div>
                                    <div className='money'>
                                        <span className='money1'> ￥</span>
                                        <span className='money2'>{goods.money}     </span>
                                        <span className='money3'>{goods.sale}</span>
                                    </div>
                                </div>
                            </div> 
                        )})
                    }
            </div>
            <div className='footer'>
                <div className='bottom'>
                    <a>登录</a>
                    <a>注册</a>
                    <a>手淘App</a>
                    <a>用户反馈</a>
                </div>
            </div>
            <Mytab />
        </div>
        
    )
        }   
        routerTo(goods) {
            this.props.history.push({pathname: `/goods/${goods.id}`, state: {data: goods}})
          }  
}

export default Home