import { Button, NavBar, TabBar,Icon } from 'antd-mobile';
import React,{Component}from 'react';
import './good.css'
import {
    NavLink, Route, Redirect,
    useHistory,
    useLocation,
    useParams,
    useRouteMatch,
    withRouter
} from 'react-router-dom'
class Goods extends Component{
    constructor(props, context) {
        super(props, context)
        console.info(props)
        this.goodsList = props.location.state.data
        this.histroy=props.location.goback
    }
//  const Goods = ()=>{
    render(props){
        var goodsList = {};
        var history;
    //     backClick(){
    //         this.props.history.go(-1);
    //    };
        return(
            <div>
                    <NavBar
                    mode="light"
                    icon={ <Icon type="left" /> }
                     onLeftClick={ () => {
                        this.props.history.push('/home');
                    } }
                    ></NavBar>
                    <div className='theimg'>
                        <img
                            src={`//gw.alicdn.com/bao/uploaded/${this.goodsList.img}jpg_500x500q90.jpg_.webp`}
                        />
                    </div>
                    <div className="dir">
                        <div className="main">
                            <p className="price">
                                <span className="num">￥{this.goodsList.money}元</span>
                            </p>
                            <p className="dirtxt">
                                <span>{this.goodsList.title}</span>
                            </p>
                        </div>
                    </div>
                <div className="bottom-bar">
                    <div className="shop">
                        <span className="iconfont icon-dianpu">
                        </span>
                        <span className="text">店铺</span>
                    </div>
                    <div className="wangwang">
                        <span className="iconfont icon-kefu "></span>
                        <span className="text">客服</span>
                    </div>
                    <div className="fav">
                        <span className="iconfont icon-shoucang"></span>
                        <span className="text">收藏</span>
                    </div>
                    <div className="cart">
                        <span className="gtitle">加入购物车</span>
                    </div>
                    <div className="buy">
                        <span className="gtitle">立即购买</span>
                        </div>
                    </div>
            </div>
        )
         }
    
}
export default Goods;